import Tasks from '@/pages/task-page.vue'
import Levels from '@/pages/levels-page.vue'
import Auth from '@/pages/auth-page.vue'
import Settings from '@/pages/settings-page.vue'
import Rating from '@/pages/rating-page.vue'
import Theory from '@/pages/theory-page.vue'

import axios from 'axios'
import store from '@/store/index.js';
import { createRouter, createWebHistory } from 'vue-router'

const routes = [
  { 
    path: '/', 
    component: Levels,
    beforeEnter: async (to, from, next) => {
      await axios.get(store.state.server_url + '/get-modules:' + localStorage.getItem('login'))
        .then(res => store.commit('updateModules', res.data))
        .catch(err => store.commit('showError', { text: 'Недійсна відповідь сервера' }))
      await axios.get(`${store.state.server_url}/modules/:${localStorage.login}/:${store.state.modules[localStorage.currentModuleIndex]}`)
        .then(res => { store.commit('updateLevels', res.data); next()})
        .catch(err => store.commit('showError', { text: 'Недійсна відповідь сервера' }))
    }
  },
  { 
    path: '/quiz', 
    component: Tasks,
    beforeEnter: async (to, from, next) => {
      await axios.get(store.state.server_url + '/get-modules:' + localStorage.getItem('login'))
        .then(res => store.commit('updateModules', res.data))
        .catch(err => store.commit('showError', { text: 'Недійсна відповідь сервера' }))
      await axios.get(`${store.state.server_url}/modules/:${localStorage.login}/:${store.state.modules[localStorage.currentModuleIndex]}/:${localStorage.currentLevelIndex}`)
        .then(res => { store.commit('updateTasks', res.data); next()})
        .catch(err => store.commit('showError', { text: 'Недійсна відповідь сервера' }))
    }
  },
  { 
    path: '/theory', 
    component: Theory,
    beforeEnter: async (to, from, next) => {
      await axios.get(store.state.server_url + '/get-modules:' + localStorage.getItem('login'))
        .then(res => store.commit('updateModules', res.data))
        .catch(err => store.commit('showError', { text: 'Недійсна відповідь сервера' }))
      await axios.get(`${store.state.server_url}/modules/:${localStorage.login}/:${store.state.modules[localStorage.currentModuleIndex]}/:${localStorage.currentLevelIndex}`)
        .then(res => { store.commit('updateTheory', res.data); next()})
        .catch(err => store.commit('showError', { text: 'Недійсна відповідь сервера' }))
    }
  },
  {
    path: '/rating',
    component: Rating,
    beforeEnter: async (to, from, next) => {
      await axios.get(`${store.state.server_url}/get-rating`)
        .then(res => { store.commit('updateRating', res.data); next()})
        .catch(err => store.commit('showError', { text: 'Недійсна відповідь сервера' }))
    }
  },
  { 
    path: '/auth', 
    name: 'auth',
    component: Auth ,
  },
  {
    path: '/settings',
    component: Settings,
  },
]

const router = createRouter({ routes, history: createWebHistory() })

router.beforeEach((to,from,next) => {
  store.commit('showLoader')
  if (!window.localStorage.isAuthorized && to.name !== 'auth') router.push('/auth').catch(()=>{})
  else next()
})
router.afterEach((to,from,next) => {
  store.commit('showLoader')
})

export default router;