<template>
  <div class="title-wrapper">
    <side-menu/>
    <div class="title">{{modules}}</div>
  </div>
  <router-link :to='(levels[i-1].type == "quiz") ? "/quiz" : "/theory"'
      v-for='i in levels.length' :key='i'
      :class='levelIsOpened(i-1) ? "level-block" : "level-block"'
      @click='setCurrentLevelIndex(levels[i-1].index)'>
    <span class="material-icons">
      {{!levelIsOpened(i-1) ? 'lock' : (levels[i-1].type == 'quiz') ? 'widgets' : 'visibility'}}
    </span>
    <span class="level-block-body">
      <div class="level-name">{{(levels[i-1].type == "theory") ? 'Конспект: ' : ''}} {{levels[i-1].name}}</div>
      <div class="level-score" :style='levels[i-1].score ? "color: gray" : "color: lightgray"'>
        {{!levels[i-1].score ? 'Не розпочато' : (levels[i-1].type == "quiz") 
          ? `${levels[i-1].score} / ${levels[i-1].maxscore} балів` : 'Переглянуто' }}
      </div>
    </span>
    <div class="progress-bar-wrapper" :style ='!levels[i-1].score 
      ? `background: lightgray` : (levels[i-1].type == "theory") 
      ? `background: #5F9EA0` : `animation: col 1s -${levels[i-1].score / levels[i-1].maxscore}s linear forwards paused`'>
        <progress-bar :ratio='levels[i-1].score / levels[i-1].maxscore' :isTicked='(levels[i-1].type == "quiz")'/>
    </div>
  </router-link>
</template>

<script>
import sideMenu from '@/components/side-menu.vue'
export default {
  components: { sideMenu },
  computed: {
    levels() { return this.$store.state.levels },
    modules() { return this.$store.state.modules[localStorage.currentModuleIndex] },
  },
  methods: {
    setCurrentLevelIndex(index) { localStorage.setItem("currentLevelIndex", index) },
    levelIsOpened(index) { return index ? !!this.levels[index-1].score : true }
  }
}
</script>

<style lang="scss" scoped>
.title-wrapper {
  display: flex;
  align-items: center;
  margin-bottom: 20px; 
}
.level-block {
  width: 100%;
  height: 6em;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  margin-bottom: 5px;
  background: white;
  border-radius: 8px;
  border: 1px solid rgba(0,0,0,.1);
}
.disabled {
  pointer-events: none;
  opacity: .5;
}
.material-icons {
  font-size: 3em !important;
  font-weight: 100 !important;
  color: #323232;
  margin: 0 .4em;
}
.level-block-body {
  height: 6em;
  display: flex;
  flex-grow: 1;
  flex-direction: column;
  justify-content: space-evenly;
  z-index: 1;
  @media (min-width: 500px) { box-shadow: 8px 0 8px -6px rgba(0,0,0,.6); }
}
.level-name {
  font-weight: bold; 
  font-size: 1.1em;
  margin: -10px 0;
}
.level-score {
  margin: -10px 0;
  color: lightgray;
}
.progress-bar-wrapper {
  height: 6em;
  width: 7em;
  border-radius: 0 8px 8px 0;
  @media (max-width: 499px) { display: none }
  div {
    width: 6em;
    height: 5em;
    margin: .5em;
  }
}
</style>