<template>
  <input type="checkbox" id="side-menu-checkbox" ref='checkbox'>
  <label for="side-menu-checkbox">
    <div class="material-icons" id="side-menu-icon">menu</div>
  </label>
  <div class="underlay" @click='$refs.checkbox.click()'></div>
  <div class="side-menu">
    <div class='side-menu-modules'>
      <div class="side-menu-header">МОЇ МОДУЛІ</div>
      <div class='side-menu-entry' v-for='i in modules.length' :key='i' @click='gotoModule(i-1)'>Математика  –  {{modules[i-1]}}</div>
    </div>
    <div class='side-menu-other'>
      <div class="side-menu-header">ІНШЕ</div>
      <router-link to='/rating' class='side-menu-entry'>Рейтинг</router-link>
      <router-link to='/settings' class='side-menu-entry'>Налаштування</router-link>
    </div>
  </div>
</template>

<script>
export default {
  computed: {
    modules() { return this.$store.state.modules }
  },
  methods: {
    gotoModule(index) { 
      localStorage.setItem("currentModuleIndex", index) 
      window.location.reload();
    }
  }
}
</script>

<style lang="scss" scoped>
.underlay {
  display: none;
  position: fixed;
  left: 0; top: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
}
.side-menu {
  width: 100%;
  z-index: 2;
  position: fixed;
  display: flex;
  justify-content: space-between;
  left: 0; top: -400px;
  transition: top .5s;
  background: rgba(#323232,.97);
  .side-menu-modules {
    margin: 1.25em 0 1.5em 3em;
  }
  .side-menu-other {
    margin: 1.25em 3em 1.5em 0;
    text-align: right;
  }
  .side-menu-header {
    line-height: 2.5em;
    font-weight: bold;
    font-size: 1.1em;
    color: white;
  }
  .side-menu-entry {
    display: block;
    line-height: 2.5em;
    color: rgba(white,.8);
    &:hover { text-decoration: underline; }
  }
}
#side-menu-checkbox {
  appearance: none;
  &:checked ~ .underlay {display: block}
  &:checked ~ .side-menu {top: 0}
}
#side-menu-icon {
  &:hover { opacity: .5; }
}

</style>