<template>
  <div class="wrapper">
    <input type='radio' 
      ref='input'
      name='pagination-panel'
      @click="$emit('taskGoto')"
      :class='ratio == undefined ? "undefined" : ratio == 1 ? "correct" : "incorrect"'
    />
    <span class="input-counter"><slot></slot></span>
  </div>
</template>

<script>
export default {
  props: ['ratio'],
  methods: {
    click() { this.$refs.input.click() }
  }
  //emits: ['click']
  //watch: {
  //  taskIndex(val) { if (this.task == val) this.$refs.input.click() }
  //},
};
</script>

<style lang="scss" scoped>
.wrapper {
  position: relative;
  display: inline-block;
}
.input-counter {
  position: absolute;
  top: 50%; left: 50%;
  transform: translate(-50%, -50%);
  pointer-events: none;
  color: #323232;
  font-weight: bold;
  font-size: 1.2em;
  text-align: center;
}
input {
  margin: auto;
  display: flex;
  align-items: center;
  justify-content: center;
  appearance: none;
  position: relative;
  width: clamp(30px, 3vw, 40px);
  height: clamp(30px, 3vw, 40px);
  text-align: center;
  border-radius: 2px;
  &:checked { background: #323232; }
  &:checked + span { color: white; }
}
.incorrect { 
  &::after {
    content: "";
    position: absolute;
    background: #323232;
    width: 24px;
    height: .15em;
    border-radius: 1px;
  }
  &:checked::after { background: white; }
  &::after { transform: rotate(-45deg); transform-box: center; }
}
</style>