<template>
  <div id='task-title-wrapper'>
    <router-link to="/" class="material-icons">arrow_back</router-link>
    <span class='title'>{{level.name}}</span>
  </div>
  <modal-result
    v-model:isVisible='modalVisible' 
    :ratio='levelRatio'></modal-result>
  <div id='task-body-wrapper'>
    <div id='task-pagination-wrapper'>
      <div id="task-pagination">
        <task-pagination-cell
          v-for='i in level.tasks.length' :key='i'
          @taskGoto="taskGoto(i-1)" ref='page'
          :ratio='taskRatios[i-1]'>{{i}}</task-pagination-cell>
      </div>
      <div id="task-stats" v-if='levelFinished==1' :style='`animation: col 1s -${task.correct/task.total}s linear forwards paused`'>
        З цим завданням впоралося &thinsp; <strong>{{Math.round(task.correct/task.total*100)}}%</strong> &thinsp; учасників &thinsp; <strong>({{task.correct}} з {{task.total}})</strong>
      </div>
    </div>
    <div id="task-body">
      <div id="task-text">{{task.text}}</div>
      <div id="task-grid-A">
        <div id="task-items" v-if='task.items'>
          <div class="task-option" v-for='(item, index) in task.items' :key='index'>{{1 + index}} {{item}}</div>
        </div >
        <div id="task-options" v-if='task.options'>
          <div v-for='(option, index) in task.options' :key='index'>
            <div class="task-option">{{String.fromCharCode(1040 + index)}} {{option.includes('https://') ? '' : option}}</div>
            <img v-if='option.includes("https://")' :src='option'>
          </div>
        </div>
        <div id="task-image" v-if='task.image'>
          <img :src='task.image' alt="loading...">
        </div>
      </div>
      <div id="task-grid-B">
        <div id="task-tab">
          <task-table 
            :answer='answers[taskIndex]' 
            :isCorrect='levelCompleted ? taskBools[taskIndex] : undefined' 
            :isInput='level.tasks[taskIndex].options !== undefined'/>
        </div>
          <button-plain @click='this.$refs.page[Math.min(this.taskIndex+1,this.level.tasks.length-1)].click()' :style='`${this.levelCompleted ? "background: rgba(0,0,0,.25); box-shadow: 0 0 white" : ""}`'>Далі</button-plain>
          <button-plain v-if='levelCompleted && !levelFinished' @click='levelCheck()'>Перевірити</button-plain>
          <button-plain v-if='levelFinished' @click='modalVisible=true'>Результат</button-plain>
      </div>
    </div>

  </div>
</template>

<script>
import axios from 'axios'
import taskTable from '@/components/task-table.vue'
import taskPaginationCell from '@/components/task-pagination-cell.vue'
export default {
  components: { taskTable, taskPaginationCell },
  data() { return {
      answers: [],
      taskIndex: 0,
      taskRatios: [],
      taskBools: [undefined], //
      levelRatio: undefined,
      levelFinished: false,
      modalVisible: false,
  }},
  beforeMount() {
    this.$nextTick(_ => MathJax.Hub.Queue(["Typeset", MathJax.Hub]))
    if (this.level.answers == undefined)
      this.answers = this.level.tasks.map(a => Array.from(a.key).fill(undefined))
    else {
      this.answers = this.level.answers
      this.levelFinish()
    }
  },
  mounted() { this.$refs.page[0].click() },
  computed: {
    levelCompleted() { return !this.answers.flat().includes(undefined) },
    level() { return this.$store.state.level },
    task() { return this.$store.state.level.tasks[this.taskIndex] },
  },
  methods: {
    taskGoto(val) {
      this.taskIndex = val;
      this.$nextTick(_ => MathJax.Hub.Queue(["Typeset", MathJax.Hub]))
    },
    async levelCheck() {
      this.$store.commit("updateAnswers", {index: this.$store.state.levelIndex, body: this.answers})
      await axios.post(`${this.$store.state.server_url}/publish-answer`, {
        username: localStorage.login,
        module: this.$store.state.modules[localStorage.currentModuleIndex],
        index: localStorage.currentLevelIndex,
        answers: this.answers,
        ratios: this.level.taskRatios,
        score: this.level.levelScore,
        maxscore: this.level.levelScoreMax,
      })
      await axios.post(`${this.$store.state.server_url}/publish-score`, {
        username: localStorage.login,
        score: this.level.levelScore,
      })
      this.modalVisible = true;
      this.levelFinish()
    },
    levelFinish() {
      this.levelFinished = true; 
      this.taskBools = this.level.taskBools;
      this.taskRatios = this.level.taskRatios;
      this.levelRatio = this.level.levelRatio;
    }
  },
}
</script>

<style lang = 'scss' scoped>
img { 
    max-width: 150px;
    max-height: 150px;
    height: inherit !important;
}
#task-title-wrapper {
  display: flex;
  align-items: center;
  margin-bottom: 25px;
}
#task-body-wrapper {
  background: white;
  border-radius: 8px;
  border: 1px solid lightgray
}
#task-pagination-wrapper {
  box-shadow: 0 5px 10px -4px rgba(0,0,0,.6);
}
#task-pagination { 
  z-index: 2;
  padding: clamp(10px, 1.75vh, 20px)  clamp(20px, 4vw, 40px);
  border-radius: 8px 8px 0 0;
}
#task-stats {
  padding: clamp(10px, 2vh, 20px) clamp(20px, 4vw, 40px);
  color: white;
  border-radius: 0 0 4px 4px;
}
#task-text { 
  margin-bottom: 20px; 
  line-height: 1.5em; 
}
#task-body {
  padding: clamp(25px, 3vh, 40px) clamp(20px, 4vw, 40px) 40px clamp(20px, 4vw, 40px);
  border-radius: 0 0 8px 8px;
}
#task-grid-A {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  #task-image { flex-grow: 0; }
  #task-items { flex-grow: 1; }
  #task-options { flex-grow: 1 }
  .task-option { 
    padding: .75em 3ch; 
    line-height: 1.5em;
    &::first-letter {
      font-size: 1.1em;
      font-weight: bold; 
      padding-right: 1ch;
      margin-left: -2ch;
    }
  }
}
#task-grid-B {
  display: flex;
  align-items: flex-end;
  padding-top: clamp(25px, 3vh, 40px);
  gap: 20px 0;
  @media (max-width:650px) { flex-wrap: wrap; }
  #task-tab { flex-basis: 100%; }
}

</style>
