<template>
<div class="form-wrapper">
  <form @submit.prevent='resetPassword()'>
    <div class="form-header">Зміна паролю</div>
    <input type="text" v-model='password' placeholder="Старий пароль" required>
    <input type="text" v-model='newpassword' placeholder="Новий пароль" required>
    <button type='submit' class='button-plain'>Змінити</button>
    <router-link to='/' class="form-back">Повернутися</router-link>
  </form>
</div>
</template>

<script>
import axios from 'axios'
export default {
  data() { return {
    password: undefined,
    newpassword: undefined
  }},
  methods: {
    async resetPassword() {
      await axios.post(this.$store.state.server_url + '/reset-password', { login: localStorage.login, password: this.password, newpassword: this.newpassword })
        .then(res => { this.$store.commit('showSuccess', { text: res.data.message }) })
        .catch(err => { if (err.response) this.$store.commit('showError', { text: err.response.data.message }) 
      })
    }
  },
}
</script>

<style lang="scss" scoped>
.form-wrapper {
  position: fixed;
  left: 0; 
  top: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}
form {
  width: min(400px, 80%);
  padding: clamp(40px, 8vw, 60px) 15px;
  border-radius: 8px;
  background: white;
  box-shadow: 0 0 10px -2px rgba(0,0,0,.2);
}
.form-header {
  font-weight: bold;
  font-size: 1.5em;
  margin-bottom: clamp(30px, 5vw, 40px);
  text-indent: 10px;
}
.form-back {
  margin-top: 10px;
  display: flex;
justify-content: center;
  width: 100%;
  font-size: 1em;
  text-decoration: underline;
}
input {
  position: relative;
  margin: auto;
  width: 95%;
  display: block;
  font-size: 1.1em;
  margin-bottom: clamp(20px, 5vw, 30px);
}
button {
  width: 100%;
  margin-bottom: 15px;
}
</style>