<template>
  <div class='title-wrapper'>
    <router-link to="/" class="material-icons">arrow_back</router-link>
    <span class='title'>{{level.name}}</span>
  </div>
  <div >
    <div v-for='i in level.entries.length' :key='i' class='theory-card'>
      <div class='theory-header-wrapper'>
        <span class='theory-header'>{{level.entries[i-1].header}}</span>
      </div>
      <div class="theory-body-wrapper">
        <div v-for='j in level.entries[i-1].def.length' :key='j'>
          <div class="theory-def">
            <div>{{level.entries[i-1].def[j-1]}}</div>
            <div>{{level.entries[i-1].formula[j-1]}}</div>
          </div>
          <hr v-if='j != level.entries[i-1].def.length'>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  beforeMount() {
    this.$nextTick(_ => MathJax.Hub.Queue(["Typeset", MathJax.Hub]))
    if (this.level.score == undefined) this.levelFinish()
  },
  computed: {
    level() { return this.$store.state.level },
  },
  methods: {
    async levelFinish() {
      await axios.post(`${this.$store.state.server_url}/publish-answer`, {
        username: localStorage.login,
        module: this.$store.state.modules[localStorage.currentModuleIndex],
        index: localStorage.currentLevelIndex,
        score: 10, maxscore: 10,
      })
      await axios.post(`${this.$store.state.server_url}/publish-score`, {
        username: localStorage.login,
        score: 10,
      })
    }
  }
}
</script>

<style lang="scss" scoped>
//* {border: 1px solid red}
.theory-card {
  background: white;
  border-radius: 8px;
  border: 1px solid lightgray;
  margin-bottom: 15px;
}
hr { 
  opacity: .25; 
}
.theory-header-wrapper {
  padding: clamp(12px, 2.5vh, 24px) clamp(20px, 4vw, 40px);
    box-shadow: 0 5px 10px -4px rgba(0,0,0,.6); 
}
.theory-header {
  font-size: 1.1em;
  font-weight: bold;
}
.theory-body-wrapper {
  padding: 10px clamp(20px, 4vw, 40px) 20px clamp(20px, 4vw, 40px);
}
.theory-def {
  display: flex;
  gap: 20px;
  align-items: center;
  margin-bottom: 15px;
  margin-top: 15px;
  div:nth-of-type(1) { flex-grow: 1; }
}
</style>